.faq-page {
    text-align: center;
}

.faq-intro {
    margin-bottom: 25px;
}

.faq-hero {
    max-width: 1000px;
    margin: 50px auto;
}

.faq-hero-img {
    width: 100%;
    height: auto;
}

.faq-hero-text {
    margin: 0;
}

.faq-intro-line {
    margin-left: 5vw;
    margin-right: 5vw;
    border-color: #00dbf4;
}

.faq-intro-text {
    margin: 0;
}

.faq-body {
    margin-bottom: 50px;
}

.faq-body-all-questions {
    margin-left: 5vw;
    margin-right: 5vw;
    text-align: left;
    margin-bottom: 50px;
}

.faq-body-questions-title {
    max-width: 1000px;
    margin: 0 auto;
    color: #53C651;
}

.faq-body-list {
    max-width: 1000px;
    margin: 0 auto;
}

.faq-body-list li {
    font-size: 21px;
}

.faq-body-question {
    margin: 0 auto 5px;
    text-decoration: underline;
    color: #337ab7;
    display: inline;
}

.faq-body-question:hover {
    text-decoration: none;
    color: #222;
    cursor: pointer;
}

.faq-body-all-answers {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    column-gap: 20px;
    row-gap: 30px;
    max-width: 1000px;
    margin: 0 auto;
}

.faq-body-answer {
    text-align: left;
}

.faq-body-answer-title {
    margin: 0;
    color: #d0a73d;
}

.faq-body-answer-text {
    font-size: 20px;
    margin: 5px auto;
}

.faq-body-answer-back {
    margin: 0;
    color: #337ab7;
    text-decoration: underline;
    display: inline;
}

.faq-body-answer-back:hover {
    color: #222;
    text-decoration: none;
    cursor: pointer;
}

@media only screen and (max-width: 720px) {
    .faq-body-all-answers {
        grid-template-columns: repeat(1, 1fr);
    }

    .faq-body-answer {
        margin-left: 20px;
        margin-right: 20px;
    }
}